import http from '../../../../configs/httpConfig'
import {notifyError, notifySuccessadd, notifySuccessdelete} from "../../../Toast"

export const AddPrizeMasterCricket = (add) => {
  return (dispatch) => {      
    http
    .post('/cricket/contest_prizes/add_prize', add)
    .then((response) => {
      notifySuccessadd(response.data.message)
      dispatch({
        type: 'ADD_PRIZE_MASTER_CRICKET',
        data: response.data
      })    
    })
    .catch((err) => notifyError(err.message))
  }
}

// api/cricket/contest_prizes/add_prize
