export const getcricketprizeType = (page, type) => {
  return async (dispatch) => {
    dispatch(getAllCricketcontestprize())
    await http.get(`cricket/contest_prizes/prize_list?page=${page}&type=${type}`).then((response) => {      
      dispatch({
        type: 'PRIZE_MASTER_CRICKET_TYPE',
        data: response.data.data
      })
      console.log(response.data.data.data)
    }).catch(() => {
      dispatch(getAllCricketcontestFail())
    })
  }
}

export const getprizecricketdetails = (id) => {
  return async (dispatch) => {
   await http
      .put(`/cricket/view/${id}`)
      .then((response) => {
        dispatch({
          type: 'PRIZE_DETAILS_CRICKET',
          data: response.data.data
        })
      })
      .catch((err) => console.log(err))
  }
}

