export const deleteprizecricket = (id) => {
  return () => {
    http
      .delete(`/cricket/${id}`)
      .then(() => {
        notifySuccessdelete()
        // dispatch(getAllData())
      })
      .catch((err) => {
        notifyError()
        console.log(err)
      })
  }
}

